import { useEffect, useState } from 'react';
import { Link, useParams, useNavigate } from 'react-router-dom';
import useLocales from '../../hooks/useLocales';

import ResetConfirmModal from '../../sections/ticket/ResetConfirmModal';
import { API_TICKET } from '../../config';
import axios from '../../utils/axios';

import Page from '../../components/Page';
import PageBanner from '../../components/wrappers/PageBanner';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';

export default function ManageAd() {
    const { id } = useParams();
    const { t } = useLocales();
    const navigate = useNavigate();
    const [ticket, setTicket] = useState(null);

    useEffect(() => {
        axios.get(`${API_TICKET.getTicketDetail}/${id}`).then((res) => {
            setTicket(res.data.data.ticket);
        });
    }, [id]);

    if (!ticket) return <div />;

    return (
        <Page>
            <PageBanner>
                <div className="container p-4 mb-8">
                    <div className="flex flex-col gap-8 justify-center items-center">
                        <div className="flex gap-4 w-full">
                            <Link to="/profile" className="text-primary cursor-pointer underline">
                                {t('title.menu.my_listing')}
                            </Link>
                            <div>/</div>
                            <div className="grid md:grid-cols-2 text-stone-400">
                                <p>{`1x ${ticket.event?.name}`}</p>
                            </div>
                        </div>

                        <div className="w-full">
                            <h3 className="text-[64px]">{t('title.manage_ad')}</h3>
                        </div>

                        <div className="w-full mt-8 flex flex-col gap-4">
                            <GradientBorderWrapper>
                                <div className="flex sm:flex-col gap-1 justify-start p-6">
                                    <h3 className="text-[28px] font-medium">{t("title.event")}</h3>
                                    <p className="text-stone-400 text-base">{ticket?.event?.name}</p>
                                    <p className="text-stone-400 text-base">{ticket?.event?.place}</p>
                                    <p className="text-stone-400 text-base">{`${ticket?.location?.city.name} • ${ticket?.location?.state.name} • ${ticket?.location?.country.name}`}</p>
                                </div>
                            </GradientBorderWrapper>
                        </div>

                        <GradientBorderWrapper>
                            <div className="flex flex-col gap-1 p-6">
                                <h3 className="text-[28px] font-medium">{t("title.payments")}</h3>
                                <div className="flex gap-2" >
                                    <p className="text-primary">R$ {ticket?.sellPrice}</p>
                                    <p className="text-stone-400">{`${t("label.received_on")} 14 de setembro de 2022 • ${ticket?.event?.place} , ${ticket?.event?.place}`}</p>
                                </div>
                            </div>
                            <hr className="border-t-1 border-stone-600 mx-4" />
                            <div className="flex flex-col p-6 gap-4">
                                <div className="flex flex-col gap-1" >
                                    <h3 className="text-[28px] font-medium">{t("title.bank_data")}</h3>
                                    <p className="text-stone-400">123 - 4567 - ... 890</p>
                                </div>
                                <Link to="/profile" className="text-sm text-primary underline" >{t("action.edit_account")}</Link>
                                <p className="text-sm text-stone-400" >
                                    Lorem ipsum dolor sit amet consectetur. Quis cursus leo turpis ultricies posuere sollicitudin leo. At diam volutpat in quis mi nullam eget. Proin lectus odio vitae sit a adipiscing vitae habitasse. Id quisque hendrerit sollicitudin condimentum. Placerat ridiculus pulvinar ipsum pellentesque sit hendrerit nunc.
                                </p>
                            </div>
                        </GradientBorderWrapper>

                        <GradientBorderWrapper>
                            <div className="flex flex-col gap-4 p-6">
                                <div className="flex gap-2 items-center" >
                                    <h3 className="text-[28px] font-medium">{t("title.ticket")}</h3>
                                    <div className="px-3 py-1 text-sm text-white bg-yellow-300 rounded-full" >
                                        {t("title.sold")}
                                    </div>
                                </div>
                                <Link to="#" className="text-sm text-primary underline" >{t("action.details")}</Link>
                            </div>
                        </GradientBorderWrapper>

                        <GradientBorderWrapper>
                            <div className="flex flex-col gap-4 p-6">
                                <div className="flex flex-col gap-1">
                                    <h3 className="text-[28px] font-medium">{t("title.sell_price")}</h3>
                                    <div className="flex gap-2" >
                                        <p className="text-stone-400">{`R$ ${ticket.sellPrice} ${t("description.per_ticket", { price: ticket.sellPrice * 90 / 100 })}`} </p>
                                    </div>
                                </div>
                                <Link to="#" className="text-sm text-primary underline" >{t("action.edit")}</Link>
                            </div>
                            <hr className="border-t-1 border-stone-600 mx-4" />
                            <div className="flex flex-col p-6 gap-4">
                                <div className="flex flex-col gap-1">
                                    <h3 className="text-[28px] font-medium">{t("description.additional")}</h3>
                                    <div className="flex gap-2" >
                                        <p className="text-stone-400">{`${t("label.batch")} 3`} </p>
                                    </div>
                                </div>
                                <Link to="#" className="text-sm text-primary underline" >{t("action.edit")}</Link>
                            </div>
                        </GradientBorderWrapper>

                        <div className="flex flex-col gap-8 justify-center items-center w-full">
                            <div className="w-full mb-8">
                                <div className="w-full flex justify-between gap-8">
                                    <Link to="#" className="text-[19px] text-red-700 underline" >{t("action.del_ad")}</Link>
                                    <button onClick={() => navigate(`/ticket/see/${ticket._id}`)} className="btn btn-primary btn-outline px-8 capitalize py-4 rounded-2xl font-normal text-[19px] h-auto">
                                        {t('action.go_to_ad')}
                                    </button>
                                </div>
                            </div>
                        </div>
                        <ResetConfirmModal />
                    </div>
                </div>
            </PageBanner>
        </Page>
    );
}
